import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { of, Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { environment } from '../../../environments/environment';


import { AuthResponse, Usuario } from '../interfaces/interfaces';

import* as vglobales from 'globales';

const USER_KEY = 'auth-user';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

   url = vglobales.lurl+'/api/auth/';

  private baseUrl: string = environment.baseUrl;
  public _usuario!: Usuario;
  
  public currentUser = null;

  get usuario() {
    return { ...this._usuario };
  }




  constructor( private http: HttpClient ) {


  }

  registro( name: string, email: string, password: string, area: string ) {

    const url  = `${ this.baseUrl }/auth/new`;
    const body = { email, password, name, area };

    return this.http.post<AuthResponse>( url, body )
      .pipe(
        tap( ({ ok, token }) => {
          if ( ok ) {
            localStorage.setItem('token', token! );
          }
        }),
        map( resp => resp.ok ),
        catchError( err => of(err.error.msg) )
      );

  }



  login( email: string, password: string, area: string, name: string) {

    const url  = `${ this.baseUrl }/auth`;
    const body = { email, password, area, name};

    return this.http.post<AuthResponse>( url, body )
      .pipe(
        tap( resp => {
          if ( resp.ok ) {
            localStorage.setItem('token', resp.token! );


          }
        }),
        map( resp => resp.ok ),
        catchError( err => of(err.error.msg) )
      );
  }




  validarToken(): Observable<boolean> {

    const url = `${ this.baseUrl }/auth/renew`;
    const headers = new HttpHeaders()
      .set('x-token', localStorage.getItem('token') || '' );

    return this.http.get<AuthResponse>( url, { headers } )
        .pipe(
          map( resp => {
            localStorage.setItem('token', resp.token! );
            this._usuario = {
              name: resp.name!,
              uid: resp.uid!,
              email: resp.email!,
              area: resp.area!
            }

            return resp.ok;
          }),
          catchError( err => of(false) )
        );

  }

  logout() {
    localStorage.clear();
  }



  
  public saveUser(user: any): void {
    window.sessionStorage.removeItem(USER_KEY);
    window.sessionStorage.setItem(USER_KEY, JSON.stringify(user));
  }

  public getUser(): any {
    const user = window.sessionStorage.getItem(USER_KEY);
    if (user) {
      return JSON.parse(user);
    }

    return {};
  }

  

  public isLoggedIn(): boolean {
    const user = window.sessionStorage.getItem(USER_KEY);
    if (user) {
      return true;
    }

    return false;
  }



  getUsuarios(): Observable<any> {
    return this.http.get(this.url);
  }


}
