import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../auth/services/auth.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {


  ngOnInit(): void {

    this.logueado()
  this.pruebarol()
 
  }






element = false;

normal = false;
ad = false;
tec = false;
aca = false;
sininicio = true;

  get usuario() {
    return this.authService.usuario.name;
  }

  get email() {
    return this.authService.usuario.email;
  }

 get area() {
      return this.authService.usuario.area

    }
 

  constructor( private router: Router,
               private authService: AuthService
               ) {





               }

  logout() {

    this.authService.logout();
    this.router.navigate(['/auth/login'])
    .then(() => {
      window.location.reload();
    });

  }



  login() {

    if (localStorage.getItem("currentUser") == null) {
      this.element=false;
    } else{
      this.element=true;
    }

  }

  pruebarol(){



    if (this.area == 'usuario') {
      console.log(this.area);
      this.normal=true;
    }
    else if(this.area == 'admin'){
      console.log(this.area);
      this.ad=true;
    }
    else if(this.area == 'tecnica'){
      console.log(this.area);
      this.tec=true;
    }
    else if(this.area == 'academico'){
      console.log(this.area);
      this.aca=true;
    }



  }


 logueado() {
  if (localStorage.getItem("token") == null) {
    this.element=false;
    this.sininicio=true;
  }
  else{
    this.element=true;
    this.sininicio=false;
  }

}






}









